
import defaultAvatar from '@/assets/img/header/default_avatar.png';
import Vue from 'vue';

export default Vue.extend({
  props: {
    user: {
      type: Object as () => { headImg: string, gmtModifyTime: number, name: string },
      required: true,
    },
    time: {
      type: Number,
    },
  },
  functional: true,
  render(_h, { props: { user, time }, parent }) {
    return <div class="annotation-user-info">
      <base-image defaultImage={defaultAvatar}
        class="user-avatar" src={user.headImg} />
      <div class="user">
        <div class="user-name text-one-line">{ user.name }</div>
        {
          !time ? null
            : <div class="user-time">
              { parent!.$filters.date(time, 'MM/DD/YYYY HH:mm') }
            </div>
        }
      </div>
    </div>;
  },
});

