
import { i18n } from '@/i18n';
import { defineComponent, watch, ref } from '@vue/composition-api';
import CommentDetail from '../comment/CommentDetail.vue';
import AnnotationUser from './AnnotationUser.vue';
import { Sign } from './type.d';

type Image = {
  process: number,
  url: string,
  path: string,
};
const INNER_VALUE = Symbol('innerValue');
export default defineComponent({
  props: {
    readonly: {
      type: Boolean,
      default: false,
    },
    closeable: {
      type: Boolean,
      default: true,
    },
    autosize: {
      default() {
        return { maxRows: 15 };
      },
    },
    annotation: {
      type: Object as unknown as () => Sign,
      required: true,
    },
  },
  model: {
    prop: 'annotation',
    event: 'change',
  },
  setup(props) {
    const images = ref([] as Image[]);

    watch(() => {
      return props.annotation;
    }, (annotation) => {
      // @ts-ignore
      if (annotation[INNER_VALUE]) {
        return;
      }
      const arr = annotation.accessoryInfo?.images ?? [];
      images.value = arr.map((url: string) => ({
        process: 100,
        url,
        path: url.split('?')[0].split('/').slice(-2).join('/'),
      }));
    }, {
      immediate: true,
    });
    return { images };
  },
  render() {
    const {
      readonly, annotation, images, closeable, autosize,
    } = this;
    return <div class="comment-dialog">
      <div class="comment-dialog-header">
        <AnnotationUser style={{ flex: 1 }}
          user={annotation.modifierInfo}
          time={annotation.gmtModifyTime} />
        <div class="comment-dialog-header-tools">
          {
            closeable ? <i
              class="iconfont icon-delete"
              onClick={() => { this.$emit('close'); }} /> : null
          }
        </div>
      </div>
      <CommentDetail
        style={{}}
        autosize={autosize}
        readonly={readonly}
        value={{ content: annotation.content, imgs: images }}
        imgLimit={4}
        empty={images.length ? '' : i18n.t('model.detail.annotation.no_content')}
        onViewImages={(...args: any) => this.$emit('viewImages', ...args)}
        onChange={({ content, imgs }: { content: string, imgs: Image[] }) => {
          this.images = imgs;
          this.$emit('change', {
            ...annotation,
            content,
            accessoryInfo: {
              ...annotation.accessoryInfo,
              images: imgs.map(item => item.url),
            },
            [INNER_VALUE]: true,
          });
        }}
        atUsers={[]} />
    </div>;
  },
});
