

import { defineComponent } from '@vue/composition-api';
// @ts-ignore;
import AtTa from 'vue-at/dist/vue-at-textarea';
import selectFiles from '@/utils/select-files';
import validateFile from '@/utils/validators/valid-file';
import OSSUploader from '@/utils/oss/uploader';
import { i18n } from '@/i18n';
import useEl from '_/composables/use-el';
import { lf2p } from '@/filters';
import Scrollable from '@/components/Scrollable.vue';

type Image = {
  url: string,
  path?: string,
  blob?: Blob,
  process?: number,
};
type User = {
  name: string,
  id: string,
  avatar: string,
};
type Value = {
  content: string,
  imgs: Image[]
};
export default defineComponent({
  props: {
    autosize: {
      default() {
        return { maxRows: 15 };
      },
    },
    atUsers: {
      type: Array as () => User[],
      default: [] as User[],
    },
    defaultAvatar: {
      type: String,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    empty: {
      type: String,
      default: '',
    },
    imgLimit: {
      type: Number,
      default: Infinity,
    },
    value: {
      type: Object as () => Value,
      required: true,
    },
  },
  model: {
    event: 'change',
    prop: 'value',
  },
  setup(props, { emit }) {
    const getAts = (val: string) => {
      const reg = /@\S*/g;
      const members: User[] = [];
      val.replace(reg, (account) => {
        const found = props.atUsers.find(user => account === user.name);
        if (found) {
          members.push(found);
        }
        return '';
      });
      return members;
    };

    const emitChange = (v: Partial<Value>) => {
      const value = {
        ...props.value,
        ...v,
      };
      emit('change', value, {
        ats: getAts(value.content),
      });
    };

    const uploadFile = async () => {
      const rawFiles = await selectFiles(['png', 'jpg', 'gif']);
      if (props.value.imgs.length + rawFiles.length > props.imgLimit) {
        throw new Error(i18n.t('model.comment.image_count_limit', { number: props.imgLimit }));
      }
      let files = [];
      for (let i = 0; i < rawFiles.length; i++) {
        const rawFile = rawFiles[i];
        await validateFile(rawFile, 'img');
        files.push({
          url: URL.createObjectURL(rawFile),
          blob: rawFile,
          path: undefined,
          process: 0,
        });
      }
      files.map(async file => {
        const { key, url } = await OSSUploader.send({
          sourceType: 'BINARY_FILE',
          body: file.blob,
          onUpload: (_, __, ___, process) => {
            file.process = Math.round(process * 100);
          },
        });
        file.url = url;
        file.path = key;
        file.process = 100;
        emitChange({});
      });
      files = [...props.value.imgs, ...files];
      emitChange({ imgs: files });
    };

    const el = useEl();

    const previewImages = (index: number) => {
      emit('viewImages', props.value.imgs.map(item => item.url), {
        index,
        targetElems: el.value?.querySelectorAll('.thumb'),
      });
    };

    const removeImg = (index: number) => {
      const files = [...props.value.imgs];
      files.splice(index, 1);
      emitChange({ imgs: files });
    };

    return {
      removeImg,
      uploadFile,
      previewImages,
      input(val: string) {
        emitChange({ content: val });
      },
    };
  },
  render() {
    const {
      atUsers, value, uploadFile, input, readonly,
      removeImg, empty, previewImages, imgLimit,
      autosize,
      $scopedSlots,
    } = this;
    const $t = this.$t.bind(this);

    const atUserSlot = $scopedSlots['at-member-item'] ?? (({ item }: { item: User }) => {
      return [
        <base-image class="user-avatar"
          src={item.avatar} fit="cover" />,
        <el-tooltip
          content={item.name} placement="right">
          <span>{item.name}</span>
        </el-tooltip>,
      ];
    });
    const descContent = <div class="desc">
      {value.content ? <div domPropsInnerHTML={lf2p(value.content)} />
        : <div class="empty">{empty}</div>}
    </div>;
    const desc = autosize ? descContent
      : <Scrollable y class="desc-wrapper">{descContent}</Scrollable>;
    return <div class="host comment-detail">
      {
        readonly ? desc : <AtTa
          members={atUsers ?? []}
          nameKey="name"
          scopedSlots={{
            item: atUserSlot,
            default: () => (
              <el-input type="textarea"
                value={value.content}
                vFocus
                onInput={input}
                maxlength="300"
                autosize={autosize}
                show-word-limit
                placeholder={atUsers.length ? $t('model.comment.input_placeholder') : $t('model.detail.annotation.some_message')} />
            ),
          }}
        />
      }

      <div class="imgs">
        {
          !value.imgs.length && !readonly ? <div onClick={uploadFile} class="placeholder">
            <i class="iconfont icontupian" />
            {' '}
            添加图片
          </div> : null
        }
        {
          value.imgs.map((item, i) => (<a class="thumb" data-data={JSON.stringify(item)}>
            <div class="tools">
              <i class="iconfont icon-zoom" onClick={() => previewImages(i)} />
              {readonly ? null : <i class="iconfont icon-delete" onClick={() => removeImg(i)} />}
            </div>
            <base-image src={item.url} class="img" fit="cover" />
          </a>))
        }
        {
          value.imgs.length && !readonly && imgLimit > value.imgs.length ? <a class="btn-add" onClick={uploadFile}>
            <i class="iconfont icon-add" />
          </a> : null
        }
      </div>
    </div>;
  },
});
